<template>
	<div id="userCenter">
		<shop-header />
		<!---->
		<shop-search :showClassify="false" pageName="" />

		<!---->
		<section class="user-content">
			<div class="min-width flex flex-x-between">
				<div class="user-content-l">
					<ul>
						<li>
							<strong></strong>
							<router-link to="/userCenter/orderList"></router-link>
							<router-link to="/userCenter/orderEvaluate"></router-link>
							<router-link to="/userCenter/orderPay"></router-link>
							<router-link to="/userCenter/ofenBuy"></router-link>
						</li>
						<li>
							<strong></strong>
							<router-link to="/userCenter/collectionPro"></router-link>
							<router-link to="/userCenter/collectionStore"></router-link>
							</p>
						</li>
						<li>
							<strong></strong>
							<!--<router-link to="/userCenter/feedback"></router-link>-->
							<router-link to="/userCenter/contactService"></router-link>
						</li>
						<li>
							<strong></strong>
							<router-link to="/userCenter/userSetting"></router-link>
							<router-link to="/userCenter/address"></router-link>
						</li>
					</ul>
				</div>
				<div class="user-content-r">
					<router-view></router-view>
				</div>
			</div>
		</section>

		<shop-footer />

	</div>
</template>
<script>
	import ShopTop from '../components/header.vue'
	import ShopFooter from '../components/footer.vue'
	import btn from '../components/shopbtn.vue'
	import Search from '../components/search.vue'

	import { Badge, Dialog, Table, TableColumn, InputNumber } from 'element-ui'
	import mixin from '../../utils/mixin.js'
	export default {
		mixins: [mixin],
		components: {
			[ShopTop.name]: ShopTop,
			[ShopFooter.name]: ShopFooter,
			[btn.name]: btn,
			[Search.name]: Search,

			// element component
			[Badge.name]: Badge,
			[Dialog.name]: Dialog,
			[Table.name]: Table,
			[TableColumn.name]: TableColumn,
			[InputNumber.name]: InputNumber,

		},
		data() {
			return {
				navbarText: ''
			}
		},
		created() {},
		mounted() {

		},
		methods: {

		}
	}
</script>
<style lang="scss">
	@import '../../assets/css/userCenter.scss';
	.router-link-active {
		color: #FF4400 !important;
	}
</style>


