export default {
  data() {
    return {
      carIds: [], // id
      ShippingcarList: [], // 
      alreadyChosenGoods: [], // 
      orderTotal: 0 // 
    }
  },
  methods: {
    /**
     * 
     */
    createShippingcarBySku(d) {
      //			
      //			if(d.stockLimit == '1' && d.canUseQuantity <= 0) {
      //				this.$message('')
      //				return false
      //			}

      var data = d
      var saveurl = 'shippingcar/create'
      this.postRequest('shippingcar/list', {
        buyerId: this.userInfo.typeCode,
      }).then(d => {
        if (d.data && d.data.state == 0) {
          let a = []
          let ids = []
          var params = {
            buyerId: this.userInfo.typeCode,
            productId: data.productId,
            productName: data.productName,
            skuId: data.id || data.skuId,
            skuName: data.name || data.skuName,
            quantity: 1,
            comment: data.remark || '',
            sellerId: data.sellerId || data.supplierId || this.sellerId,
          }

          for (let j of d.data.aaData) {
            if (j.sellerId == params.sellerId) {
              a.push(j.skuId)
              var o = {
                skuId: j.skuId,
                id: j.id
              }
              ids.push(o)
            }
          }

          // 
          if (this.isInArray(a, params.skuId)) {
            saveurl = 'shippingcar/update'
            for (var l of ids) {
              if (params.skuId == l.skuId) {
                params.id = l.id
              }
            }
          } else {
            saveurl = 'shippingcar/create'
          }

          if (params.sellerId) {
            this.postRequest(saveurl, params).then(d => {
              if (d.data && d.data.state == 0) {
                this.$message({
                  message: '',
                  type: 'success'
                });
                // 
                this.getShopCarCount()
              } else {
                this.$message.error('');
              }
            })
          } else {
            this.$message.error('');
          }
        }
      })
    },
    /**
     * 
     */
    createShippingcar(skus) {
      var count = 0
      var saveurl = 'shippingcar/create'

      this.postRequest('shippingcar/list', {
        buyerId: this.userInfo.typeCode,
      }).then(d => {
        if (d.data && d.data.state == 0) {
          let a = []
          let ids = []
          for (let j of d.data.aaData) {
            if (j.sellerId == this.sellerId) {
              a.push(j.skuId)
              var o = {
                skuId: j.skuId,
                id: j.id
              }
              ids.push(o)
            }
          }

          for (let i = 0; i < skus.length; i++) {
            var params = {
              buyerId: this.userInfo.typeCode,
              productId: skus[i].productId,
              productName: skus[i].productName,
              skuId: skus[i].id,
              skuName: skus[i].name,
              quantity: skus[i].quantity,
              comment: skus[i].remark,
              sellerId: this.sellerId,
            }


            // 
            if (this.isInArray(a, params.skuId)) {
              saveurl = 'shippingcar/update'
              for (var l of ids) {
                if (params.skuId == l.skuId) {
                  params.id = l.id
                }
              }
              this.postRequest(saveurl, params).then(d => {
                if (d.data && d.data.state == 0) {
                  this.$message('');
                  this.$emit('submitShopCar')
                  // 
                  this.getShopCarCount()
                }
              })
            } else {
              saveurl = 'shippingcar/create'
              this.postRequest(saveurl, params).then(d => {
                if (d.data && d.data.state == 0) {
                  count++
                  if (count == skus.length) {
                    this.$emit('submitShopCar')
                    // 
                    this.getShopCarCount()
                  }
                } else {
                  this.$message.error('');
                }
              })
            }
          }
        }
      })
    },
    /**
     * 
     */
    getShopCarCount() {
      this.postRequest('shippingcar/list', {
        buyerId: this.userInfo.typeCode,
      }).then(d => {
        if (d.data && d.data.state == 0) {
          this.$store.dispatch('getCount_AC', d.data.dataCount)
        } else {
          this.$message.error(d.data.msg)
        }
      })
    },
    /**
     * skuid
     */
    getShopCarSkuId() {
      this.postRequest('shippingcar/list', {
        buyerId: this.userInfo.typeCode,
      }).then(d => {
        if (d.data && d.data.state == 0) {
          let a = []
          for (let i of d.data.aaData) {
            let o = {
              sellerId: i.sellerId,
              skuId: i.skuId
            }
            a.push(o)
          }
        }
      })
    },
    /**
     * 
     */
    getShippingcarList() {
      this.postRequest('shippingcar/queryGrouplist', {
        buyerId: this.userInfo.typeCode,
      }).then(d => {
        if (d.data && d.data.state == 0) {
          this.ShippingcarList = d.data.aaData
        } else {
          this.$message.error(d.data.msg)
        }
      })
    },
    /**
     * 
     */
    del(sellerId) {
      this.postRequest('shippingcar/queryGrouplist', {
        buyerId: this.userInfo.typeCode,
        sellerId: sellerId
      }).then(d => {
        if (d.data && d.data.state == 0) {
          for (var i in d.data.aaData) {
            if (sellerId == i) { //idid
              var items = d.data.aaData[i]
              for (var j in items) {
                this.deleteShopCarById(items[j].id)
              }
            }
          }
        }
      })
    },
    /**
     * byId
     */
    deleteShopCarById(id, iscardel) {
      this.postRequest('shippingcar/delete', {
        id: id
      }).then(d => {
        if (d.data && d.data.state == 0) {
          if (iscardel) {
            this.$message('')
            this.getStoreCar()
          }
        } else {
          this.$message.error(d.data.msg)
        }
      })
    },
    /**
     * 
     */
    getOrderTotal(arr) {
      var priceTotal = 0;
      var quantityTotal = 0;
      for (var i in arr) {
        quantityTotal += arr[i].quantity
        if (arr[i].promotionPrice > 0 && arr[i].promotionBasePrice > 0) {
          priceTotal += arr[i].quantity * (arr[i].promotionPrice - 0)
        } else {
          priceTotal += arr[i].quantity * (arr[i].price - 0)
        }
      }

      return {
        priceTotal: priceTotal.toFixed(2),
        quantityTotal: quantityTotal
      }
    },
    /**
     * 
     */
    isInArray(arr, element) {
      for (var i = 0; i < arr.length; i++) {
        if (arr[i] == element) {
          return true;
        }
      }
      return false;
    },
    /**
     * 
     */
    orderCancel(id, cb, type,data) {
      var orderStatusId = 7
      var tipsText = '?'
      var message = '!'

      if (type == 'qrsh') {
        orderStatusId = 4
        tipsText = '?'
        message = '!'
        this.$alert.confirm(tipsText, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          let inspectedOrderTotal = 0
          for(let k of data.orderitemList){
            k.inspectedWeight = k.quantity
            k.inspectedTotal = k.priceForDecimal.mul(k.inspectedWeight).mul(100) 
            inspectedOrderTotal += k.inspectedTotal
          }
          data.inspectedOrderTotal = inspectedOrderTotal

          //  
          this.postRequest('orderitem/batchUpdate', {
            orderitems: JSON.stringify(data.orderitemList),
            arr: true
          }).then(resp => {
            if (resp.data && resp.data.state == 0) {
              this.postRequest('order/update', {
                id: id,
                orderStatusId: orderStatusId,
                inspectedOrderTotal: data.inspectedOrderTotal
              }).then(resp => {
                if (resp.data && resp.data.state == 0) {
                  this.$message({
                    type: 'success',
                    message: message
                  });
                  cb && cb()
                }
              })
            }
          })
        }).catch(() => {});
      } else {
        this.$alert.confirm(tipsText, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          this.postRequest('order/update', {
            id: id,
            orderStatusId: orderStatusId
          }).then(resp => {
            if (resp.data && resp.data.state == 0) {
              this.$message({
                type: 'success',
                message: message
              });
              cb && cb()
            }
          })
        }).catch(() => {});
      }
    },
    /**
     * 
     */
    countVisits() {
      this.postRequest('store/countVisits', {
        sellerId: this.sellerId
      }).then(d => {
        if (d.data && d.data.state == 0) {}
      })
    },
    /**
     * toShopCar
     */
    toShopCar() {
      this.$router.push({
        name: 'shoppingCar'
      })
    },
    /**
     * 
     */
    toProductList() {
      this.$router.push({
        name: 'productList',
        query: {
          gbgoodsId: 'oftenbuy',
          gbgoodsName: ''
        }
      })
    },
    /**
     * 
     */
    toUserCenter() {
      this.$router.push({
        name: 'userCenter'
      })
    },
    /**
     * 
     */
    toCollectionPro() {
      this.$router.push({
        name: 'collectionPro'
      })
    },
    /**
     * 
     */
    toContactService() {
      this.$router.push({
        name: 'contactService'
      })
    },
    /**
     * 
     */
    toIndex() {
      if (this.userInfo) {
        let toName
        switch (this.userInfo.userType) {
          case '005':
            toName = 'PersonalBuyCenter'
            break;
          case '002': /** */
            toName = 'RestaurantIndex'
          break;
          case '003':
            toName = 'RestaurantIndex'
          break;
          case '004':
            toName = 'RestaurantIndex'
          break;
          case '008':
            toName = 'RestaurantIndex'
          break;
          default:
            toName = 'northRingIndex'
            break;
        }
        this.$router.push({
          name: toName
        })
      } else {
        this.$router.push({
          name: 'northRingIndex'
        })
      }
    }
  }
}



